*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

// Fonts
@font-face {
  font-family: 'MonteCarlo';
  src: url('../../../public/fonts/MonteCarlo.ttf')
}

@font-face {
  font-family: 'LobsterTwo';
  src: url('../../../public/fonts/LobsterTwo.ttf')
}

html {
  width: 100%;
  font-size: 62.5%;
  color: #dfdfdf;
  background: url(../../../public/images/fireworks-frame-cutted.png) center fixed;
  background-size: cover;
  background-repeat: no-repeat;

  @include respond(phone) {
    font-size: 31%; // 1 rem = 5px, 5/16 = 31%
  }
  @include respond(tab-port) {
    font-size: 50%; // 1 rem = 8px, 8/16 = 50%
  }
  @include respond(tab-land) {
    font-size: 56.25%; // 1 rem = 9px, 9/16 = 56%
  }
  @include respond(normal-desktop) {
    font-size: 62%; // 1 rem = 10px, 10/16 = 62%
  }
  @include respond(big-desktop) {
    font-size: 68%; // 1rem = 11, 11/16 = 68%
  }
}

body {
  width: 100%;
  font-family: 'LobsterTwo';
}

.button {
  margin: 0 auto;
  padding: 0.2rem;
  border: none;
  border-radius: 5%;
  height: 3rem;
  width: 10rem;
  background-color: $color-golden;
  font-family: 'LobsterTwo'
}
