@import "../../sass/main.scss";

.trivia {
  background-color: black;
  padding-top: 2rem;
  padding-bottom: 2rem;
  padding-left: 5%;
  padding-right: 5%;
  font-size: 2rem;

  &__title {
    text-align: left !important;
    color: $color-golden;
  }

  &__border {
    border: 2px solid $color-golden;
    border-radius: 0.4rem;
  }

  &__carousel {
    height: 25rem;

    &__slide {
      position: absolute;
      height: 25rem;
      width: 85%;
      overflow: scroll;
      padding: 1rem;

      background-color: black;
      color: $color-golden;

      &--prev {
        z-index: 1;
        animation: swipe-prev 0.5s;
      }

      &--current {
        z-index: 2;
        animation: swipe-current 0.5s;
      }

      &__radio-list {
        font-size: 2rem;
        margin-bottom: 0.5rem;
      }
    }
  }

  &__button {
    display: block;
  }
}

@keyframes swipe-prev {
  from {
    @include translate(0, 0);
  }

  to {
    @include translate(-100%, 0);
  }
}

@keyframes swipe-current {
  from {
    @include translate(100%, 0);
  }

  to {
    @include translate(0, 0);
  }
}