@import "../../sass/main.scss";

.menu {
  &__button {
    position: fixed;
    z-index: 99;
    top: 1rem;
    right: 1rem;
  
    background-color: $color-golden;
    height: 7rem;
    width: 7rem;
    border-radius: 50%;

    &__background {
      &--expand {
        animation: expand 0.5s;
      }

      &--contract {
        animation: contract 0.5s;
      }
    }
  }

  &__nav {
    background-color: $color-golden;
    z-index: 9;
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    overflow: scroll;

    &--expand {
      animation: show .1s;
    }
  }

  &__bar {
    background: url(../../../public/images/flowers-yellow.jpg);
    background-position: bottom;
    background-size: cover;
    // background-color: white;

    padding-left: 1rem;
  
    &__icon {
      vertical-align: middle;
      height: 6rem;
    }

    &__title {
      display: inline;
      vertical-align: middle;
      margin-left: 1rem;
      font-size: 2.5rem;
      color: white;
    }
  }

  &__title {
    margin-top: 2rem;
    margin-bottom: 2rem;
    font-size: 3rem;
  }

  ul {
    list-style: none;
  }
  
  &__item {
    width: 80%;
    margin-left: 10%;
    margin-bottom: 2rem;
    border-bottom: 2px solid white;

    text-align: center;
    text-decoration: none;
    font-size: 3rem;

    &__link {
      text-decoration: none;
      color: white;
    }
  }
}

@keyframes expand {
  from {
    position: fixed;
    top: 1rem;
    right: 1rem;
    height: 7rem;
    width: 7rem;
    border-radius: 50%;
    background-color: $color-golden;
  }

  to {
    position: fixed;
    top: -100%;
    right: -100%;
    height: 500%;
    width: 500%;
    border-radius: 50%;
    background-color: $color-golden;
  }
}

@keyframes contract {
  from {
    position: fixed;
    top: -100%;
    right: -100%;
    height: 500%;
    width: 500%;
    border-radius: 50%;
    background-color: $color-golden;
  }

  to {
    position: fixed;
    top: 1rem;
    right: 1rem;
    height: 7rem;
    width: 7rem;
    border-radius: 50%;
    background-color: $color-golden;
  }
}

@keyframes show {
  0% {
    opacity: 0;
  }

  99% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}