@import "../../sass/main.scss";

.countdown {
  padding-top: 2rem;
  padding-bottom: 2rem;

  &__title {
    margin: 0 auto;
    width: fit-content;
    font-size: 3rem;
  }

  &__container {
    @include display-flex;
    justify-content: space-around;
    width: 90%;
    margin-top: 1rem;
    margin-left: 5%;
    font-size: 2rem;
  }

  &__item {
    @include center-flex;
    flex-flow: column;
    height: 7rem;
    width: 7rem;
    border: 3px solid $color-golden;
    border-radius: 50%;

    &__number {
      color: white;
    }

    &__time {
      font-size: 1rem;
      color: grey;
    }
  }
}
