@import "../../sass/main.scss";

.attendance {
	margin-bottom: 5rem;
	padding-left: 5%;
	padding-right: 5%;

  font-size: 2rem;
	font-weight: 300;

	&__title {
		font-weight: inherit;
	}

	&__input {
		height: 3rem;
		width: 100%;
		margin-bottom: 2rem;
		border: none;
		border-bottom: 1px solid black;
	}

	&__button-box {
		@include display-flex;
		justify-content: space-around;
		margin-top: 3rem;
	}
}