@import "../../sass/main.scss";

// Generic classes
.title {
  width: 100%;
  font-size: 4rem;
  text-align: center;
  margin-bottom: 1rem;
  color: $color-golden-light;
  font-weight: 400;
}

.subtitle {
  font-size: 3rem;
  color: $color-golden;
}

.circle-image {
  &__box {
    width: 60%;
    padding-top: 60%;
    position: relative;
    margin: 0 auto;
  
    &__image {
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;

      height: 100%;
      width: 100%;
      border-radius: 50%;
      object-fit: cover;

      @include box-shadow;
    }
  }
}

.music-button {
  position: fixed;
  z-index: 5;
  bottom: 1rem;
  left: 1rem;

  background-color: $color-golden;
  height: 5rem;
  width: 5rem;
  border-radius: 50%;

  &__icon {
    height: 50%;
    width: 50%;
    margin-right: 7%
  }
}

// Specific classes
.title-section {
  position: relative;
  height: 35rem;
  overflow: hidden;
  
  &__blur-background {
    position: absolute;
    z-index: -1;
    // background-image: url("../../../public/images/video-background.png");

    height: 100%;
    width: 100%;

    filter: blur(8px);
    -webkit-filter: blur(8px);

    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;

  }

  // &__image {
  //   position: absolute;
  //   top: 5%;
  //   left: 5%;
  //   height: 90%; 
  //   width: 90%;
  //   object-fit: cover;
  // }

  &__video-box {
    height: 100%; 
    width: 100%;
    object-fit: cover;
  }

  &__video {
    height: 100%; 
    width: 100%;
    object-fit: cover;
  }

  &__text {
    position: absolute;
    z-index: 1;
    top: 5%;
    left: 5%;
    height: 90%;
    width: 90%;

    @include center-flex;
    flex-flow: column;
    gap: 2rem;

    font-size: 2rem;
    color: white;
    border: 2px solid white;
    border-radius: 0.2rem;

    &__title {
      font-size: 5.5rem !important;
      font-weight: bold;
    }
  }

  h1 {
    font-size: 5rem;
    font-family: 'MonteCarlo';
  }
}

.description {
  margin-top: 2rem;
  padding: 5%;

  &__title {
    font-size: 3rem;
    text-align: center;
  }

  &__text {
    font-size: 2rem;
    text-align: center;
  }
}

.carousel {
  height: 40rem;
  width: 100%;

  &__slides {
    height: 40rem;
    width: 100%;
    object-fit: cover;
  }
}

.vows {
  padding-top: 5rem;
  padding-bottom: 5rem;
  padding-left: 5%;
  padding-right: 5%;

  font-size: 2rem;
  text-align: center;

  &__title {
    font-size: 3rem;
  }
}

.timeline {
  position: relative;
  z-index: -1;
  width: 90%;
  margin-left: 5%;
  margin-bottom: 5rem;
  padding-top: 3rem;
  padding-bottom: 3rem;

  border-radius: 7px;

  background-color: white;
  font-size: 3rem;

  table {
    border-collapse: collapse;
  }

  tr {
    height: 6rem;
  }

  &__dot-box {
    position: relative;
    width: 15%;
    display: table-cell;
    vertical-align: top;
    padding-top: 1rem;

    &--first {
      padding-top: 0;
    }

    &--last {
      padding-top: 0;
      vertical-align: bottom;
    }
  }

  &__heart {
    @include center-flex;
    height: 4rem;
    width: 4rem;
    margin: 0 auto;

    border-radius: 50%;
    font-size: 2rem;
    background-color: black;

    &--small {
      height: 3rem;
      width: 3rem;
      font-size: 1.2rem;
    }
  }

  &__line {
    @include center-absolute;
    height: 100%;
    width: 1rem;
    z-index: -1;
    background-color: grey;
  }

  &__title {
    margin-top: 1rem;
    padding-left: 5%;
    padding-right: 5%;

    font-size: 2.5rem;
    color: black;
  
    &--last {
      font-size: 2rem;
      padding-left: 5%;
      padding-right: 5%;

      color: grey
    }
  }

  &__date {
    margin-bottom: 1rem;
    padding-left: 5%;
    padding-right: 5%;

    font-size: 1.3rem;
    color: $color-golden;
    font-family: 'LobsterTwo';
  }

  &__description {
    margin-bottom: 1rem;
    padding-left: 5%;
    padding-right: 5%;

    font-size: 1.6rem;
    color: grey;
  }
}

.mass {
  margin-bottom: 5rem;

  &__title {
    margin-top: 1rem;
    padding-left: 5%;
    padding-right: 5%;

    font-size: 2.5rem;
    text-align: center;
  }

  &__date {
    margin-bottom: 1rem;
    padding-left: 5%;
    padding-right: 5%;

    text-align: center;
    font-size: 1.3rem;
    color: $color-golden;
    font-family: 'LobsterTwo';
  }

  &__image-box {
    position: relative;
  }

  &__image {
    margin-left: 15%;
    width: 70%;
    object-fit: cover;
    border-radius: 0.5rem;

    @include box-shadow;
  }

  &__background {
    position: absolute;
    top: 15%;
    z-index: -1;

    height: 70%;
    width: 100%;
    
    background-color: $color-golden;
  }
}

.reception {
  &__image {
    width: 100%;
    object-fit: cover;
    margin-bottom: 2rem;
  }
}

.godparents {
  margin-bottom: 7rem;

  &__parent-box {
    position: relative;
    margin-bottom: 5rem;
  }

  &__subtitle {
    margin-top: 1rem;
    text-align: center;
    color: white;
  }

  &__background {
    position: absolute;
    top: 40%;
    z-index: -1;
    height: 60%;
    width: 100%;
    
    background-color: $color-pink;

    &--male {
      background-color: #007cba;
    }
  }
}

.gift-table {
  position: relative;
  z-index: -1;
  width: 90%;
  margin-left: 5%;
  margin-bottom: 5rem;
  padding-top: 3rem;
  border-radius: 7px;

  background-color: white;
  font-size: 3rem;

  &__image {
    width: 60%;
    margin-left: 20%;
  }
}

.ar-frame {
  position: relative;
  height: 40rem !important;
}